export default {
	name: 'ReferenceItem',
	props: {
		item: Object,
		index: Number,
		type: String
	},
	computed: {
		lastAudio: {
			set (audio) {
				this.$store.commit('setReferenceAudio', audio)
			},
			get () {
				return this.$store.state.referenceAudio
			}
		}
	},
	methods: {
		onClickItem () {
			const newUrl = `/sounds/${this.type}s/${this.index + 1}.mp3`
			let lastUrl = null
			if (this.lastAudio) {
				lastUrl = new URL(this.lastAudio.src).pathname
				if (lastUrl !== newUrl) {
					this.lastAudio.pause()
					this.lastAudio.currentTime = 0
				}
			}
			if (!this.lastAudio || lastUrl !== newUrl) {
				this.lastAudio = new Audio(newUrl)
			}
			if (this.lastAudio.paused) {
				this.lastAudio.play()
			}
		}
	}
}
